* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  text-transform: capitalize;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
section {
  padding: 100px 0;
}
.container {
  width: 85%;
  margin: auto;
}
@media(max-width: 767px) {
  .container {
    width: 98%;
  }
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
  color: black;
}


nav {
  border-bottom: 2px solid #747272;
  margin-bottom: 10px;
  padding: 25px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: white;
}
nav .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
nav .container .logo {
  width: 30%;
}
@media(max-width: 767px) {
  nav .container .logo {
    width: 50%;
  }
}
nav .container ul {
  width: 70%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-weight: bold;
}
@media(max-width: 767px) {
  nav .container ul {
    display: none;
  }
  nav .container .show {
    display: block;
    position: absolute;
    bottom: -250px;
    left: 0;
    background-color: white;
    z-index: 10000;
    width: 100%;
  }
  nav .container .show li {
    padding: 10px;
  }
}
nav .container .icon {
  width: 40px;
  padding: 2px;
  display: none;
  cursor: pointer;
}
@media(max-width: 767px) {
  nav .container .icon {
    display: block;
  }
}
nav .container .icon span {
  display: block;
  width: calc(100% - 4px);
  height: 2px;
  margin: 2px auto;
  background-color: black;
}

.about {
  background-image: url(techgoast.github.io/ultra/static/media/about-bg.4b35a985.webp);
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: center;
}
.about .container {
  display: flex;
  justify-content: flex-end;
}
@media (max-width: 767px) {
  .about .container {
    justify-content: center;
  }
}
.about .content {
  width: 50%;
}
@media (max-width: 767px) {
  .about .content {
      width: 80%;
  }
}
.about .content span {
  font-weight: bold;
  font-size: 25px;
}
.about .content h2 {
  color: orangered;
  padding: 30px 0;
}
.about .content p {
  line-height: 1.6;
  color: #747272;
  margin: 20px 0;
  font-weight: bold;
}

.home {
  background-image: url(techgoast.github.io/ultra/static/media/home-bg.be8b953f.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.home .container {
  min-height: calc(100vh - 200px);
  display: flex;
  justify-content: center;
  align-items: center;
}
.home .info {
  width: 60%;
  margin: auto;
  text-align: center;
}
@media (max-width: 767px) {
  .home .info {
    width: 90%;
  }
}
.home .info h1 {
  color: #747272;
  font-size: 50px;
}
@media (max-width: 767px) {
  .home .info h1 {
    font-size: 35px;
  }
}
.home .info .title {
  padding: 30px 0;
  color: orangered;
  font-weight: bold;
  font-size: 25px;
}
@media (max-width: 767px) {
  .home .info .title {
    font-size: 20px;
  }
}
.home .info .desc {
  color: #747272;
  font-weight: bold;
  line-height: 1.6;
  font-size: 18px;
  padding: 20px 0;
}
@media (max-width: 767px) {
  .home .info .desc {
    font-size: 16px;
  }
}
.home button {
  padding: 10px 20px;
  background-color: orangered;
  font-size: 20px;
  font-weight: bold;
  color: white;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
@media (max-width: 767px) {
  .home button {
    font-size: 18px;
  }
}
.portfolio {
  text-align: center;
}
.portfolio h2 {
  color: #747272;
  padding: 20px 0;
}
.portfolio .links {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}
.portfolio .links .link {
  padding: 10px 30px;
  cursor: pointer;
}
@media (max-width: 767px) {
  .portfolio .links .link {
    padding: 10px;
  }
}
.portfolio .links .active,
.portfolio .links .link:hover {
  background-color: orangered;
  color: white;
}
.portfolio .images {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  position: relative;
}
.portfolio .images .image {
  width: 25%;
  position: relative;
}
@media (max-width: 991px) {
  .portfolio .images .image {
    width: 40%;
  }
}
@media (max-width: 767px) {
  .portfolio .images .image {
    width: 80%;
  }
}
.portfolio .images .image .overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #ff450087;
  display: none;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
.portfolio .images .image img {
  max-width: 100%;
}
.portfolio .images .image button {
  position: absolute;
  padding: 10px 30px;
  border: 1px solid #747272;
  background-color: white;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  display: none;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
  cursor: pointer;
}
.portfolio .images .image:hover .overlay,
.portfolio .images .image:hover button {
  display: block;
}

.profile .container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.profile .data,
.profile .skills {
  width: 50%;
}
@media (max-width: 767px) {
  .profile .data,
  .profile .skills {
    width: 80%;
  }
  .profile .skills {
    margin-top: 50px;
  }
}
.profile .data div {
  display: flex;
  align-items: center;
  margin-top: 10px;
}
.profile .data div h4 {
  width: 20%;
}
.profile .data div p span {
  color: red;
  text-transform: lowercase;
}
.profile .skills .skill .show {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  position: relative;
}
.profile .skills .skill .progress {
  height: 2px;
  background-color: orangered;
}

.social {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-bottom: 0;
}
.social > div {
  width: calc(100% / 3);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 100px 0;
}
@media (max-width: 767px) {
  .social > div {
    width: 100%;
    padding: 50px;
  }
}
.social .face {
  background-color: dodgerblue;
}
.social .twitter {
  background-color: cornflowerblue;
}
.social .pint {
  background-color: crimson;
}
.social .icon i {
  padding: 10px;
  background-color: white;
  margin-right: 10px;
  color: #747272;
}
.social .text {
  color: white;
  font-size: 20px;
  font-weight: bold;
}

.work h2 {
  padding: 50px 0;
  color: #747272;
}
.work .items {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .work .items  {
    justify-content: center;
  }
}
.work .item {
  width: 30%;
  text-align: center;
  border: 1px solid #747272;
}
@media (max-width: 767px) {
  .work .item  {
    width: 80%;
    margin-top: 15px;
  }
}
.work .item .icon {
  padding: 20px 0;
}
.work .item h4 {
  font-size: 20px;
  color: orangered;
}
@media (max-width: 767px) {
  .work .item h4 {
    font-size: 18px;
  }
}
.work .item hr {
  width: 60%;
  margin: 20px auto;
}
.work .item p {
  padding: 0 10px 50px;
  font-size: 14px;
}

.contact {
  padding: 165px 0 20px;
  text-align: center;
}
.contact form {
  width: 70%;
  margin: auto;
  padding: 20px 0 0;
}
.contact form input {
  display: block;
  padding: 5px;
  margin: 10px 0;
  outline: none;
}
.contact .flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.contact .flex input {
  width: 49%;
}
.contact .subject,
.contact textarea {
  width: 100%;
  outline: none;
}
.contact textarea {
  height: 200px;
  background-color: #ddd;
}
.contact [type="submit"] {
  width: 50%;
  margin: 10px auto;
  cursor: pointer;
}

footer {
  text-align: center;
  padding: 10px 0;
  background-color: #ddd;
  color: #747272;
}

